    height: 37px;
  }
  .bookshelf {
    font-size: 20px;
  }
}

@media only screen and (min-width: 1366px) {
  .main-h1 {
    font-size: 49px;
    margin-top: 73px;
  }
  .input {
    width: 387px;
    height: 37px;
    margin-top: 82px;
  }
  .search-button {
    width: 156px;
    height: 37px;
    font-size: 19px;
  }
}

@media only screen and (min-width: 1920px) {
  .main-h1 {
    margin-top: 80px;
  }
  .input {
    margin-top: 89px;
  }
