.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  font-family: "Roboto";
  margin-bottom: 48px;
}

.card img {
  width: 184px;
  height: 271px;
  box-shadow: -12px -7px 13px rgba(0, 0, 0, 0.16);
  -webkit-box-shadow: -12px -7px 13px rgba(0, 0, 0, 0.16);
  -moz-box-shadow: -12px -7px 13px rgba(0, 0, 0, 0.16);
  margin-bottom: 2px;
}

.card-right {
  width: 226px;
  height: 264px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  background-color: #e69f00;
  border: 1px solid #707070;
  box-shadow: -5px 3px 6px rgba(0, 0, 0, 0.16);
  position: relative;
}

.card-right h1 {
  font-size: 18px;
  margin-top: 12px;
  margin-left: 12px;
  text-align: left;
  display: inline;
  position: absolute;
  left: 0;
  max-width: 160px;
}
.card-right svg {
  fill: white;
  display: inline-block;
  width: 31px;
  height: 31px;
  position: absolute;
  right: 0;
  margin-top: 19px;
  margin-right: 13px;
}

.card-rigth-top {
  min-height: 60px;
}

.author,
.publisher {
  font-size: 16px;
}

.button {
  color: white;
  border-radius: 14px;
  border: none;
  background-color: #755713;
  box-shadow: none;
  padding: 8px 10px;
  font-size: 18px;
  font-family: "Roboto";
  width: 149px;
  height: 38px;
  position: relative;
  top: 10px;
}

