    height: 264px;
    border-radius: 20px;
    margin-bottom: 78px;
  }
  .card img {
    position: relative;
    bottom: 12px;
    z-index: 1;
    /* float: right; */
  }
  .card-right {
    border-radius: 20px;
    border: none;
    box-shadow: none;
  }
}

