.input {
  background-color: #ffd475;
  width: 185px;
  height: 21px;
  border-radius: 20px;
  border: 1px solid #707070;
  margin-top: 22px;
  font-family: "Ravie";
  padding-left: 15px;
  font-size: 12px;
  padding-right: 30px;
  color: #ff6f00;
  font-size: 16px;
}
.header {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

input:focus {
  outline: none;
}
.main-h1 {
  font-family: "Ravie";
  margin-top: 23px;
  color: #ff6f00;
  font-size: 23px;
  text-decoration: none;
}
.bookshelf {
  margin: 0;
}

.search-button {
  height: 21px;
  display: block;
  margin: 0 auto;
  font-size: 15px;
  color: #ff6f00;
  border: 1px solid #707070;
  width: 98px;
  margin-top: 10px;
  border-radius: 10px;
  font-family: "Ravie";
  background-color: #ffd475;
  border-radius: 110px 116px;
  padding-bottom: 20px;
  outline: none;
  text-decoration: none;
  /* display: inline-block; */
}

span {
  position: relative;
  right: 20px;
  margin: 0;
  padding: 0;
  font-size: 18px;
  top: 2px;
  color: blue;
  font-weight: 500;
  cursor: pointer;
  color: #ff6f00;
  display: inline-block;
}

@media only screen and (min-width: 480px) {
  .main-h1 {
    font-size: 27px;
  }
  .input {
    width: 226px;
    height: 26px;
    margin-top: 37px;
  }
  .search-button {
    width: 148px;
    height: 26px;
    font-size: 19px;
    padding-bottom: 25px;
    margin-top: 16px;
  }
}

@media only screen and (min-width: 720px) {
  .main-h1 {
    font-size: 39px;
    margin-top: 41px;
  }
  .input {
    width: 264px;
    height: 29px;
    position: relative;
    display: inline-block;
  }
  .search-button {
    font-size: 19px;
    display: inline-block;
    margin-left: 15px;
    width: 142px;
    height: 29px;
  }
  span {
    right: 30px;
  }
  .bookshelf {
    margin: auto;
  }
}
@media only screen and (min-width: 768px) {
  .main-h1 {
    font-size: 44px;
  }
  .input {
    margin-top: 58px;
    width: 278px;
    height: 37px;
  }
  .search-button {
    width: 141px;
    line-height: 33px;
    height: 37px;
    font-size: 20px;
    padding-bottom: 5px;
    display: inline-block;
  }
  span {
    font-size: 22px;
    font-weight: 700;
  }
}

@media only screen and (min-width: 1024px) {
  .main-h1 {
    margin-top: 65px;
    font-size: 48px;
  }
  .input {
    width: 312px;
    height: 37px;
  }
  .bookshelf {
    font-size: 20px;
  }
}

@media only screen and (min-width: 1366px) {
  .main-h1 {
    font-size: 49px;
    margin-top: 73px;
  }
  .input {
    width: 387px;
    height: 37px;
    margin-top: 82px;
  }
  .search-button {
    width: 156px;
    height: 37px;
    font-size: 19px;
  }
}

@media only screen and (min-width: 1920px) {
  .main-h1 {
    margin-top: 80px;
  }
  .input {
    margin-top: 89px;
  }
}
